.add-todo {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 10px 0 10px 10px;
  position: relative;
  width: 88%;
  -ms-flex-item-align: end;
      align-self: flex-end;
}

.add-todo--text {
  padding: 5px;
  width: 90%;
  border: none;
  outline: none;
  background: transparent;
  font-size: 1.2em;
  font-style: italic;
  font-weight: 100;
  color: #242424;
}

::-webkit-input-placeholder {
  color: rgba(0, 0, 0, 0.4);
}

:-ms-input-placeholder {
  color: rgba(0, 0, 0, 0.4);
}

::-ms-input-placeholder {
  color: rgba(0, 0, 0, 0.4);
}

::placeholder {
  color: rgba(0, 0, 0, 0.4);
}

.add-todo--submit {
  width: 10%;
  border: none;
  outline: none;
  background: transparent;
  cursor: pointer;
  font-size: 1.5em;
  color: rgba(0, 0, 0, 0.4);
  -webkit-transition: 0.15s;
  -o-transition: 0.15s;
  transition: 0.15s;
}

.add-todo--submit:hover {
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
  -webkit-transition: 0.15s;
  -o-transition: 0.15s;
  transition: 0.15s;
  color: #6BB3CA;
}




